const amap = require('../../libs/amap-wx.js')
export default {
	data() {
		return {
			inputShowed: false,
			inputVal: "",
			amapPlugin: null,
			key: "6799b5f6f88d3d9fb52ac244855a8759",
			lat: 22.63137,
			lng: 114.010857,
			covers: [],
			address: [],
			scrollH: 256
		}
	},
	onLoad(options) {
		this.amapPlugin = new amap.AMapWX({
			key: this.key
		});
		const that = this;
		setTimeout(() => {
			let winHeight = uni.getSystemInfoSync().windowHeight;
			this.scrollH = winHeight - 44 - uni.upx2px(600);
			this.getLocation(() => {
				this.getPoiAround(options.key || "加油站")
			});
		}, 300)
	},
	methods: {
		trim: function(value) {
			return value ? value.toString().replace(/(^\s*)|(\s*$)/g, "") : value;
		},
		showInput() {
			this.inputShowed = true
		},
		hideInput() {
			this.inputVal = "";
			this.inputShowed = false;
			uni.hideKeyboard(); //强行隐藏键盘
		},
		clearInput() {
			this.inputVal = ""
		},
		inputTyping: function(e) {
			this.inputVal = e.detail.value
		},
		getLocation(callback) {
			const that = this
			this.amapPlugin.getRegeo({
				success: (data) => {
					that.lng = data[0].longitude;
					that.lat = data[0].latitude;
					callback();
				},
				fail: (info) => {
					callback();
				}
			})
		},
		getPoiAround(keywords) {
			//检索周边的POI	
			uni.showLoading({
				title: "加载中..."
			})
			const that = this;
			setTimeout(() => {
				this.amapPlugin.getPoiAround({
					querykeywords: keywords,
					location: '', //location： 经纬度坐标。 为空时， 基于当前位置进行地址解析。 格式： '经度,纬度'
					success: (data) => {
						let arr = [];
						let addr = [];
						for (let i = 0; i < data.markers.length; i++) {
							arr.push({
								id: i,
								latitude: data.markers[i].latitude,
								longitude: data.markers[i].longitude,
								title: data.markers[i].name
							})
							let tel = that.trim(data.poisData[i].tel);
							if (~tel.indexOf(";")) {
								tel = tel.split(";")[0]
							}
							addr.push({
								id: i,
								latitude: data.markers[i].latitude,
								longitude: data.markers[i].longitude,
								title: data.markers[i].name,
								address: data.markers[i].address,
								tel: tel,
								distance: data.poisData[i].distance
							})
						}

						this.address = addr;
						this.covers = arr;
						uni.hideLoading()
					},
					fail: (info) => {
						uni.showToast({
							title: '获取位置信息失败，请检查是否打开位置权限'
						})
						uni.hideLoading()
					}
				})
			}, 0);

		},
		bindInput: function(e) {
			const keywords = e.detail.value;
			this.getPoiAround(keywords);
		},
		marker: function(e) {
			const that = this
			const item = that.address[e.markerId || 0];
			const menu = item.tel ? ["打电话", "到这里"] : ["到这里"];

			uni.showActionSheet({
				itemList: menu,
				success(res) {
					if (res.tapIndex == 0 && item.tel) {
						uni.makePhoneCall({
							phoneNumber: item.tel
						})
					} else {
						const latitude = Number(item.latitude)
						const longitude = Number(item.longitude)
						uni.openLocation({
							name: item.title,
							address: item.address,
							latitude,
							longitude,
							scale: 18
						})
					}
				},
				fail(res) {
					console.log(res.errMsg)
				}
			})

		},
		call(event) {
			const index = Number(event.currentTarget.dataset.id);
			const tel = this.address[index].tel;
			if (tel) {
				uni.makePhoneCall({
					phoneNumber: tel
				})
			}

		},
		go(event) {
			const index = Number(event.currentTarget.dataset.id)
			const item = this.address[index];
			const latitude = Number(item.latitude)
			const longitude = Number(item.longitude)
			uni.openLocation({
				name: item.title,
				address: item.address,
				latitude,
				longitude,
				scale: 18
			})
		}
	}
}
